import {createApp} from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

//notify
import Notifications from '@kyvg/vue3-notification'

//i18n
import {languages} from '@/locales/i18n'
import {defaultLocale} from '@/locales/i18n'
import {createI18n, useI18n} from "vue-i18n";

const localeStorageLang = localStorage.getItem('lang')

const messages = Object.assign(languages)

const i18n = createI18n({
    legacy: false,
    fallbackLocale: 'en',
    locale: localeStorageLang || defaultLocale,
    messages
})


// Vuetify
import '@mdi/font/css/materialdesignicons.css'
import 'vuetify/styles'
import {createVuetify} from 'vuetify/dist/vuetify'
import * as components from 'vuetify/components'
import * as directives from 'vuetify/directives'

const vuetify = createVuetify({
    components,
    directives,
    icons: {
        defaultSet: 'mdi',
    },
})


const app = createApp(App, {
    setup() {
        const {t} = useI18n();
        return {t}
    }
});

app
    .use(store)
    .use(i18n)
    .use(Notifications)
    .use(router)
    .use(vuetify)
    .mount('#app')

// Use HTML5 history mode
if (window.history && window.history.pushState) {
    app.config.devtools = true;
    router.beforeEach((to, from, next) => {
        const fullPath = window.location.href
        if (fullPath.includes("#")) {
            next("/")
        } else {
            next()
        }
    })
    window.addEventListener('popstate', () => {
        app._routeChanged = true
        router.go(app._routeChanged ? 0 : -1)
        app._routeChanged = false
    }, false)
    router.replace(window.location.pathname.replace(process.env.BASE_URL, "/"))
}
