import {createStore} from 'vuex'
import axios from "axios";

export default createStore({
    state: {
        posts: [],
        loading: false,
        currentPage: 1,
        postsPerPage: 6,
    },
    getters: {
        pagedPosts: state => {
            const startIndex = (state.currentPage - 1) * state.postsPerPage;
            return state.posts.slice(startIndex, startIndex + state.postsPerPage);
        }
    },
    mutations: {
        setPosts: (state, posts) => state.posts = posts,
        SET_LOADING: (state, payload) => state.loading = payload,
    },
    actions: {
        async FETCH_POSTS({commit}) {
            commit('SET_LOADING', true)
            try {
                const {data: posts} = await axios.get('https://jsonplaceholder.typicode.com/posts?_limit=20')
                commit('setPosts', posts);
            } catch (err) {
                console.log(err);
            } finally {
                commit('SET_LOADING', false)
            }

        },
    },
    // actions: {
    //     async FETCH_POSTS({commit}) {
    //         const response = await axios.get('https://jsonplaceholder.typicode.com/posts');
    //         const posts = response.data;
    //         commit('setItems', posts);
    //     }
    // },
    modules: {}
})
